#details {
  position: relative;
  display: flex;
  flex-direction: column;
}
#details .details-header {
  width: 100%;
  height: 100vh;
  position: relative;
}
#details .details-header-image {
  width: 100%;
  height: 100vh;
}
#details .details-header-image::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, transparent 0, #000);
}
#details .details-header-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.65;
}
#details .details-header-details {
  position: absolute;
  bottom: 0;
  z-index: 999;
  display: flex;
  flex-direction: column;
  color: #fff;
  font-size: 2rem;
  padding: 10rem;
}
#details .details-header-details .details-header-details-title {
  font-size: 3rem;
  font-weight: 500;
}
#details .details-content {
  padding: 10rem 0;
  color: #fff;
  display: flex;
  flex-direction: column;
}

/* OVERVIEW  */
#details .details-content .details-content-overview {
  padding-bottom: 5rem;
  margin-bottom: 5rem;
  border-bottom: 2px solid #252934;
}
#details
  .details-content
  .details-content-overview
  .details-content-overview-title {
  font-size: 2.5rem;
  letter-spacing: 0.25rem;
  font-weight: 500;
  position: relative;
  margin-bottom: 3rem;
  padding-bottom: 1rem;
}
#details
  .details-content
  .details-content-overview
  .details-content-overview-title::after {
  content: '';
  display: block;
  width: 16rem;
  height: 1px;
  background: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
}
#details
  .details-content
  .details-content-overview
  .details-content-overview-content {
  font-size: 1.75rem;
}

/* CAST */
#details .details-content .details-content-cast {
  padding-bottom: 5rem;
  margin-bottom: 5rem;
  border-bottom: 2px solid #252934;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-header
  .details-content-cast-title {
  font-size: 2.5rem;
  letter-spacing: 0.25rem;
  font-weight: 500;
  position: relative;
  margin-bottom: 3rem;
  padding-bottom: 1rem;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-header
  .details-content-cast-title::after {
  content: '';
  display: block;
  width: 16rem;
  height: 1px;
  background: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .details-content-cast-item {
  border: 2px solid #252934;
  height: 24.5rem;
  width: 15rem !important;
  background-color: #252934;
  position: relative;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .details-content-cast-item
  a {
  width: 100%;
  height: 21rem;
  position: absolute;
  top: 0;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .details-content-cast-item
  a
  img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.5;
  transition: all 0.25s linear;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .details-content-cast-item
  .details-content-cast-item-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  font-size: 1.5rem;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .details-content-cast-item
  .details-content-cast-item-footer
  p {
  margin-bottom: 0;
  color: #ddd;
  width: 100%;
  display: flex;
  justify-content: center;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .details-content-cast-item
  a
  img:hover {
  opacity: 1;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .slick-slide {
  width: 200px !important;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .slick-track {
  display: flex !important;
  margin: 0 !important;
  align-content: center;
  position: relative;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .slick-prev {
  z-index: 999;
  left: 95.5% !important;
  top: -25% !important;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .slick-next {
  z-index: 999;
  left: 97.5% !important;
  top: -25% !important;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .slick-prev::before {
  content: '❮' !important;
  color: #fff !important;
  opacity: 1 !important;
  font-size: 3rem !important;
  transition: all 0.25s linear;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .slick-next::before {
  content: '❯' !important;
  color: #fff !important;
  opacity: 1 !important;
  font-size: 3rem !important;
  transition: all 0.25s linear;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .slick-arrow {
  font-size: 0 !important;
  width: 0 !important;
  height: 0 !important;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .slick-arrow:hover {
  color: #ddd !important;
}
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .slick-prev:hover::before,
#details
  .details-content
  .details-content-cast
  .details-content-cast-content
  .slick-next:hover::before {
  cursor: pointer;
  color: var(--main-red) !important;
}

/* TRAILERS */
#details .details-content .details-content-trailers {
  padding-bottom: 5rem;
  margin-bottom: 5rem;
  border-bottom: 2px solid #252934;
}
#details
  .details-content
  .details-content-trailers
  .details-content-trailers-header
  .details-content-trailers-title {
