  margin-bottom: 3rem;
  padding-bottom: 1rem;
}
#details
  .details-content
  .details-content-trailers
  .details-content-trailers-header
  .details-content-trailers-title::after {
  content: '';
  display: block;
  width: 16rem;
  height: 1px;
  background: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
}
#details .details-content .details-content-trailers-content {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

#details
  .details-content
  .details-content-trailers-content
  .details-content-review-item {
  background: rgba(37, 41, 52, 0.8);
  border: 2px solid #1b242f;
  padding: 5rem;
  word-break: break-word;
  width: 100%;
}

#details
  .details-content
  .details-content-trailers-content
  .details-content-review-item
  .details-content-review-item-content {
  font-size: 1.75rem;
}

#details
  .details-content
  .details-content-trailers
  .details-content-trailers-content
  .details-content-trailers-item {
  height: 21rem;
  position: relative;
}

#details
  .details-content
  .details-content-trailers
  .details-content-trailers-content
  .details-content-trailers-item
  iframe {
  width: 100%;
  height: 100%;
  border: none !important;
}

#details
  .details-content
  .details-content-trailers
  .details-content-trailers-content
  .slick-slide {
  height: 100% !important;
}
#details
  .details-content
  .details-content-trailers
  .details-content-trailers-content
  .slick-slide:not(:last-child) {
  margin-right: 4.5rem;
}
#details
  .details-content
  .details-content-trailers
  .details-content-trailers-content
  .slick-slider {
  width: 100%;
}
#details
  .details-content
  .details-content-trailers
  .details-content-trailers-content
  .slick-track {
  display: flex !important;
  align-content: center;
  position: relative;
  margin: 0 !important;
}

/* REVIEWS */
#details .details-content .details-content-reviews {
  margin-bottom: 5rem;
}
#details
  .details-content
  .details-content-reviews
  .details-content-reviews-header
  .details-content-reviews-title {
  font-size: 2.5rem;
  letter-spacing: 0.25rem;
