  margin-bottom: 3rem;
  padding-bottom: 1rem;
}
#details
  .details-content
  .details-content-reviews
  .details-content-reviews-header
  .details-content-reviews-title::after {
  content: '';
  display: block;
  width: 16rem;
  height: 1px;
  background: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
}
#details
  .details-content
  .details-content-reviews
  .details-content-reviews-content
  .details-content-review-item:not(:last-child) {
  margin-bottom: 5rem;
}
#details
  .details-content
  .details-content-reviews
  .details-content-reviews-content
  .details-content-review-item {
  background: rgba(37, 41, 52, 0.8);
  border: 2px solid #1b242f;
  padding: 5rem;
  word-break: break-word;
}
#details
  .details-content
  .details-content-reviews
  .details-content-reviews-content
  .details-content-review-item
  .details-content-review-item-title {
  margin-bottom: 2rem;
  font-size: 2rem;
