}
#details
  .details-content
  .details-content-reviews
  .details-content-reviews-content
  .details-content-review-item
  .details-content-review-item-content {
  font-size: 1.75rem;
}
#details
  .details-content
  .details-content-reviews
  .details-content-reviews-content
  .details-content-review-item
  a {
  color: var(--main-red);
  font-size: 1.75rem;
  transition: all 0.3s linear;
  text-decoration: none;
}
#details
  .details-content
  .details-content-reviews
  .details-content-reviews-content
  .details-content-review-item
  a:hover {
  color: #fff;
  text-decoration: none;
}

#details .details-content .details-content-trailers .slick-arrow {
  font-size: 0 !important;
  width: 0 !important;
  height: 0 !important;
}
#details .details-content .details-content-trailers .slick-prev::before {
  content: '❮' !important;
  color: #fff !important;
  opacity: 1 !important;
  font-size: 3rem !important;
  transition: all 0.25s linear;
}
#details .details-content .details-content-trailers .slick-next::before {
  content: '❯' !important;
  color: #fff !important;
  opacity: 1 !important;
  font-size: 3rem !important;
  transition: all 0.25s linear;
}
#details .details-content .details-content-trailers .slick-prev:hover::before,
#details .details-content .details-content-trailers .slick-next:hover::before {
  cursor: pointer;
  color: var(--main-red) !important;
}

